.modal-background {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  z-index: 10;
}

.modal-child {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.end {
    display: flex;
    flex-direction: column;
    background-color: papayawhip;
    justify-content: center;
    align-items: center;
    width: 400px;
    height: 100px;
}