.spectrum-container {
    display: flex;
    flex-direction: column;
    height: 28%;
}

.spectrum {
    display: flex;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    border: 1px solid black;
    background-image: linear-gradient(to right, white, orange);
}

.half {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50%;
}

